	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>Revise Smart Lenses tap into a key issue many glasses-wearing people experience, eye strain. Eye strain is a group of symptoms which occur in some people after extended use of the eyes. With the increased usage of computers, Computer Vision Syndrome (the medial term for digital eyestrain) is becoming the most common computer-related repetitive strain injury. Symptoms include; blurred vision, pain around the yes, burning sensation in the eyes, red eyes or dryness and headaches. While this not only causes physical pain, but reduces productivity.</p>\n<p>Revise Smart Lenses work to prevent the wearer from developing symptoms of eyestrain by targeting the behaviours that work to contribute to eyestrain itself. Using colour tints around the perimeter of the lens, they notify the wearer when they participate in unhealthy eye behaviours. These behaviours are lumped into 3 categories; proximity, poor lighting and prolonged use. </p>\n<p>An accompanying application serves as a customization tool for the lens alerts, allowing the wearer to change the intensity of the colour tint. In the same vein, the wearer can use the calendar to block out periods of time in which having multicoloured glasses might not be the most appropriate. The application also suggests eye exercises with each strain alert to promote mindfulness and good eye health.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 827px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 241.8379685610641%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Revise Website Mockup\"\n        title=\"\"\n        src=\"/the-next-website/static/rsolakis-revise02-f1744cdf7b18a004e35e811f57b2dd27-ae56c.jpg\"\n        srcset=\"/the-next-website/static/rsolakis-revise02-f1744cdf7b18a004e35e811f57b2dd27-5054c.jpg 375w,\n/the-next-website/static/rsolakis-revise02-f1744cdf7b18a004e35e811f57b2dd27-58467.jpg 750w,\n/the-next-website/static/rsolakis-revise02-f1744cdf7b18a004e35e811f57b2dd27-ae56c.jpg 827w\"\n        sizes=\"(max-width: 827px) 100vw, 827px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 74.95%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Healthy Eyes Cookbook\"\n        title=\"\"\n        src=\"/the-next-website/static/rsolakis-revise-03-534c159c3ad5305bd568c844808052ea-35578.jpg\"\n        srcset=\"/the-next-website/static/rsolakis-revise-03-534c159c3ad5305bd568c844808052ea-21994.jpg 375w,\n/the-next-website/static/rsolakis-revise-03-534c159c3ad5305bd568c844808052ea-f7c9d.jpg 750w,\n/the-next-website/static/rsolakis-revise-03-534c159c3ad5305bd568c844808052ea-35578.jpg 1500w,\n/the-next-website/static/rsolakis-revise-03-534c159c3ad5305bd568c844808052ea-5ea4e.jpg 2000w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>In the way of promotional material, a website outlines the different features of the glasses and accompanying application while a short cookbook features recipes for healthy eyes. </p>\n<p>_Revise, the lenses that care for your eyes. _</p>","frontmatter":{"title":"Revise Smart Lenses","graduate":"Rachel Solakis","category":"UI/UX","category2":"Web Design","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/rsolakis-revise01-159fccc3580a5373f9b6d066d9a8e414-a7a62.jpg","srcSet":"/the-next-website/static/rsolakis-revise01-159fccc3580a5373f9b6d066d9a8e414-5a1a9.jpg 375w,\n/the-next-website/static/rsolakis-revise01-159fccc3580a5373f9b6d066d9a8e414-0908c.jpg 750w,\n/the-next-website/static/rsolakis-revise01-159fccc3580a5373f9b6d066d9a8e414-a7a62.jpg 1500w,\n/the-next-website/static/rsolakis-revise01-159fccc3580a5373f9b6d066d9a8e414-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/revise-smart-lenses/","name":"/Revise Smart Lenses/"}}

